//@import "node_modules/bootstrap-sass/assets/stylesheets/bootstrap";

$font-stack: Lato, sans-serif;

$color-text-main: #000000;
$color-link: #4A90E2;
$color-link-light: #BDD7F5;
$color-background: #BDE3F5;

$mini-z-index: 5;

$gap-normal: 20px;

/* Main
---------------------------------------------- */
body {
  font: $font-stack;
  color: $color-text-main;
  font-weight: 200;
}

.navbar {
  margin: 0;
}

#color-line {
  display: block;
  margin-bottom: $gap-normal;
  width: 100%;
  height: 100%;
}

.gap-top {
  margin-top: $gap-normal;
}

.gap-bottom {
  margin-bottom: $gap-normal;
}

.navbar-july {
  border: 0;
}

.footer {
  bottom: 0;
  height: 60px;
  width: 100%;
  margin-top: $gap-normal;

  .container {
    border-top: 1px solid $color-link-light;
  }

  .text-footer {
    margin: $gap-normal 0;
  }
}

.label {
  font-size: 100%;
  font-weight: 100;
}

/* CUSTOMIZE THE CAROUSEL
-------------------------------------------------- */
$height_carousel: 500px;

/* Carousel base class */
.carousel {
  height: $height_carousel;
  margin-bottom: 60px;

  .item {
    height: $height_carousel;
    background-color: #FFFFFF;

    img {
      position: absolute;
      top: 0;
      left: 0;
      min-width: 100%;
      height: $height_carousel;
    }
  }
}

.carousel-indicators {
  li {
    border-color: $color-link;
  }
  .active {
    background-color: $color-link;
  }
}

/* Since positioning the image, we need to help out the caption */
.carousel-caption {
  z-index: $mini-z-index + 5;
  color: $color-text-main;

  p {
    text-shadow: none;
  }
}

.carousel-control {
  .fa {
    right: 50%;
    top: 50%;
    display: inline-block;
    position: absolute;
    z-index: $mini-z-index;
    color: $color-text-main
  }
}

.carousel-control.left {
  background-image: none;
}

.carousel-control.right {
  background-image: none;
}


/* CUSTOMIZE GALLERIES
-------------------------------------------------- */

.col-lg-3 {
  text-align: center;

  a {
    display: inline-block;

    img:hover {
      border: 1px solid $color-link-light;
    }
  }
}


/* CUSTOMIZE PAGINATION
-------------------------------------------------- */
.page {
  .pagination {
    margin-left: 75%;
  }
}


/* DETAIL
-------------------------------------------------- */
.detail {
  .col-lg-6 {
    text-align: center;

    img {
      margin: 0 auto;
    }

    .gallery {
      padding-left: 0;
      margin-top: $gap-normal;
      display: inline-block;
      > li {
        display: inline-block;
        a {
          font-size: 48px;
          vertical-align: middle;
        }
      }
    }

    .attribute {
      .btn {
        font-weight: 100;
      }
    }
  }
}

/* CART
-------------------------------------------------- */

.cart {
  table {
    td {
      vertical-align: middle;
    }
  }
}